@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.App{font-family:"Roboto Slab", serif;display:flex;flex-direction:column;align-items:center}.App__title{font-size:56px;margin-bottom:50px}

.Form{background-color:#557153;width:374px;display:flex;flex-direction:column;align-items:center;border-radius:67px;box-shadow:0 4px 4px rgba(0,0,0,0.25);padding:35px 0;font-family:"Roboto", sans-serif}.Form__field-container{color:white;margin-bottom:10px}.Form__field-text{height:35px;width:230px;padding-left:20px;border-radius:30px;border-style:none;font-size:19px}.Form__field-checbox-text{color:white;font-size:25px}.label{margin-left:10px}

.container{width:260px;overflow:hidden;margin-bottom:40px}.Carousel{display:flex;flex-direction:column;align-items:center}.Carousel__list{list-style:none;margin:0;padding:0;display:flex}.Carousel__button{width:55px;height:60px;border:none;background-color:white;background-position:center;background-repeat:no-repeat;background-size:contain}.Carousel__button--prev{background-image:url(data:image/png;base64,);margin-right:25px}.Carousel__button--prev-disabled{background-image:url(data:image/png;base64,);margin-right:25px}.Carousel__button--next{background-image:url(data:image/png;base64,);-webkit-transform:rotate(180deg);transform:rotate(180deg)}.Carousel__button--next-disabled{background-image:url(data:image/png;base64,);-webkit-transform:rotate(180deg);transform:rotate(180deg)}

